@import 'fonts';

#learn-hover:hover {
  background: #ffffff;
  color: #804c9e;
  transition: .4s;
  i {
    opacity: 1;
  }
}

#home-text-blur:hover {
  opacity: 0.8;
  transition: .5s;
}

.home-scale:hover {
  transform: scale(1.5, 1.5);
  transition: .25s;
}

